import React, {useRef, useState} from 'react';

import FormWithValidation from './FormWithValidation';
import InputWithValidation from './InputWithValidation';


function EditAvatarPopup(props) {
  const avatarInputRef = useRef();
  
  const [inputValue, setInputValue] = useState({userAvatar: ''});
  
  
  // handle change
  
  function handleValueUpdate(name, value) {
    setInputValue((prevValues) => ({
      ...prevValues, [name]: value
    }));
  }
  
  
  // handle submit
  
  function handleSubmit() {
    props.onUpdateAvatar({avatar: inputValue.userAvatar});
  
    avatarInputRef.current.value = '';
  }
  
  return (
    <FormWithValidation
      title=" "
      name="edit-avatar"
      submitText={props.isLoading ? props.loadingText : ''}
      onSubmit={handleSubmit}
      ariaLabel="."
      {...props}
    >
      <InputWithValidation
        ref={avatarInputRef}
        className="form__input"
        aria-label="  ."
        isShown={props.isOpen}
        type="url"
        onUpdate={handleValueUpdate}
        name="userAvatar"
        placeholder="  "
        required
      />
      <span className="profile-avatar-input-error form__input-error"></span>
    </FormWithValidation>
  );
}

export default EditAvatarPopup;