import {useState} from 'react';

import FormWithValidation from './FormWithValidation';
import InputWithValidation from './InputWithValidation';


function AddPlacePopup(props) {
  const [inputValues, setInputValues] = useState({placeName: '', placeLink: ''});
  
  function handleValuesUpdate(name, value) {
    setInputValues((prevValues) => ({
      ...prevValues, [name]: value
    }));
  }
  
  
  function handleSubmit() {
    props.onAddPlace({
      name: inputValues.placeName,
      link: inputValues.placeLink
    })
  }
  
  return (
    <FormWithValidation
      title=" "
      name="add-place"
      submitText={props.isLoading ? props.loadingText : ''}
      onSubmit={handleSubmit}
      ariaLabel="."
      {...props}
    >
      <InputWithValidation
        className="form__input"
        aria-label="."
        isShown={props.isOpen}
        onUpdate={handleValuesUpdate}
        type="text"
        name="placeName"
        value={inputValues.name || ''}
        placeholder=""
        minLength="2"
        maxLength="30"
        required
      />
      <InputWithValidation
        className="form__input form__input_type_place-link"
        aria-label="  ."
        isShown={props.isOpen}
        onUpdate={handleValuesUpdate}
        type="url"
        name="placeLink"
        value={inputValues.link || ''}
        placeholder="  "
        required
      />
      <span className="place-link-input-error form__input-error"></span>
    </FormWithValidation>
  );
}

export default AddPlacePopup;